    'Part 321': '1.Violasは単一の国際通貨ではない。Violasの場合、各国の政府や組織が独自に仮想通貨を発行し、自国通貨と1:1の比率で裏付けることができる。これにより、同一のローカル仮想通貨経済圏に存在する複数のコミュニティからのオファリングを、異なる地域のアプリケーションに提供することが可能となる。各オファリングには独自のルールと目的があるが、異なるアプリケーションにおいて、同一のローカル仮想通貨を使用する。たとえば、あたまある商業銀行は、独自の財務アプリケーションを利用していても、同一のローカル通貨を使用している、といった具合だ。',
