    'Part 6': '第6部',
    'Part 60': '6. ビットコイン、各国のデジタル通貨、Violasリザーブ',
    'Part 61': 'Violasは、独自のネイティブコインvtokenを発行する。vtokenは、Violasブロックチェーン上で、完全にビットコインによって担保され、Violas Associationにおいて1:1のマッピングvbtcを通じて交換することが可能である。このマッピングは、エンドユーザーがvtokenをステーブルコインとして扱うために必要なものである。vtokenの主な目的は、送金契約とその取引を実行するための「ガス料金」（手数料）として機能することだが、送金契約の普及と現地のデジタル通貨取引の使用を促進するため、非常に低い料金が設定されている。Violasから発行されるすべてのvtokenにはビットコインが担保され、特定のValidator Node（特定のノードの利害関係者）によるViolas Investment Tokenへの投資、またはオンチェーンの公開取引所からの購入を通じて、Violasリザーブにロックされる。Violasのユーザーは、自分のビットコインを使って、Violasのオンチェーンの公開取引所からvtokenを購入することができる。vtokenへの投資や購入は、すべてビットコイン取引によって行われる。ビットコインは、いわば、Violasブロックチェーン上で発行されたvtokenに対する公式な監査役といえる。各国のデジタル通貨契約のプルーフオブリザーブは、現地の著名なライセンス機関によって提供される。定期的な監査レポートは、デジタル署名とともにビットコインのブロックチェーン上に記録される。Violasがサポートしているのは、同額の現地通貨を裏付けとする現地のデジタル通貨コントラクトのみであることには注意されたい。さらに、Violas上のすべてのトランザクションデータは、Validator Nodeの3分の2以上によってデジタル署名され、そのハッシュ値は毎日ビットコインブロックチェーンに保存される。',
    'Part 7': '第7部',
    'Part 70': '7. Violas Association',
    'Part 71': 'Violas Associationは、わずか3~4人の設立メンバーからスタートし、正式なローンチ前には27人が設立メンバーとして加わった。1年以内に設立メンバーは100人に達する予定である。各設立メンバーは、Violasリザーブへの100ビットコインの供託を通じて、独立したValidator Nodeを所有し、運営を行っている。Violasブロックチェーンから得られるのすべての金銭的利益は、メンバー間で均等に分配される。新しいメンバーの申請は、Violas Associationメンバーによって審査され、過半数の賛成をもって承認される。Violas Associationのすべての付則の変更には、メンバーの過半数の承認が必要である。Violas Associationの法人格は、当初はシンガポールで登記されたDECENTRAIZED CYBERSPACE FOUNDATION LIMITEDを使用していたが、スイスでViolas Associationとして正式名称での登記を申請中であり、、その後は27の加盟団体に拡大する予定である。Violas Associationは、「state」からデジタル通貨発行者としての認可が得られるまでは、Violasリザーブを管理し、技術的なロードマップと開発目標を呈示する予定である。著名なライセンスエージェントによるViolas Associationへの定期監査は、ビットコインのブロックチェーン上に記録され、エージェントとAssociationの双方によってProof-of-Proofとしてデジタル署名される。',
