    'Part 90': '9. 結論',
    'Part 91': 'Violasの目標は、Libraのブロックチェーン技術を活用することにより、何十億もの人々が銀行口座なしに日々の決済を行うことを可能にすることにある。そのためには、各国の現地通貨と現地金融アプリケーションが、安定したエコシステム上でグローバルに統合されなければならない。Violasから発行されたすべての現地デジタル通貨は、対応する現地通貨と同額の裏付けを持つ。よって、現地のデジタル通貨は、その都度の認証なしに、現地通貨と同等に使用することが可能となる。Violasは、安全性、セキュリティ、透明性、リアルタイムのトランザクション速度、監査済みの準備金、および低コストの手数料といった、あらゆる利点を兼ね備えた、最新テクノロジーによるデジタル法定通貨である。',
    'Part 92': 'Violasは、各国のデジタル通貨間の競争を促進するであろう。我々は、フリードリヒ・ハイエクの提唱する選択的通貨理論に従って、自然淘汰のプロセスを通じて、各国にさらに高度なデジタル法定通貨が誕生するものと信ずる。'
