    'The Violas blockchain':'Violas 블록체인',
    'The Violas blockchain1':'Violas 블록체인은 Libra 블록체인 최신 버전과 완전 호환 된다. Libra 블록체인이 제공하는 모든 네이티브 기능과 보안이 Violas 블록체인에서도 “그대로” 제공 된다. Violas 블록체인은, 네이티브 Libra 블록체인 기능에 추가해서, 1:1 비율로 지역 피아트 통화에 페깅 된 지역 디지털 통화를 발급하는 능력을 제공 한다. Violas 블록체인이 발급하는 모든 지역 디지털 통화들은 서로 교환이 가능하다. 예를 들면, Euro 디지털 통화를 Violas 플랫폼이 제공하는 온체인 마켓플레이스에서 USD 디지털 통화로 교환할 수 있다. Violas는 중앙 글로벌 통화가 없는 글로벌 결제 시스템이다.',
