    'Security & Privacy on the Violas Network':'Violas 네트워크의 보안/개인 정보 정책',
    'Security & Privacy on the Violas Network1':'Violas는 허락 기반 블록체인이다. 각 확인 노드는 내외부 방화벽과 표준 인터넷 보안 조치로 방어되고 있다. 사용자는 최근 거래 데이터와 확인 노드 동기화를 하는 읽기 전용 노드를 만들 수도 있다. Violas 네트워크는 LibraBFT Consensus Protocol로 방어 된다. LibraBFT Consensus Protocol은 확인 노드 중 1/3이 깨질 때까지 공격을 방어할 수 있다. 확인 노드는 클라우드들, 국가/지역들, 인트라넷들에 광범위하게 분산되어 있다. Violas 블록체인은 확인 노드 3f+1로 암호화 서명되어 있다.',
